@import './reset.css';

:root {
    --app-medium-gray: #2a2b31;
    --app-light-gray: #f9f9f9;
    --app-button-color: #ff3737;
    --app-button-hover-color: #ff0404;
}


main {
    overflow: hidden;
    height: 100vh;
}

body {
    background-color: #020202;
    background-image: url("https://www.transparenttextures.com/patterns/black-felt.png");
    font-family: 'Nunito Sans', 'Open Sans', Arial, sans-serif;
}


.container {
    width: min(100% - 30px, 1200px);
    margin-inline: auto;
}

/*----------------------------------------*/
/* Header                                 */
/*----------------------------------------*/


.flex {
    display: flex;
    justify-content: space-between;

}

header {
    height: 55px;
    line-height: 55px;
    box-shadow: var(--app-body-box-shadow);
    display: flex;
    justify-content: space-between;
    background-color: var(--app-medium-gray);

}

h1 {
    color: white;
}

h1 span {
    color: var(--app-button-color);
}

nav ul {
    display: flex;
    justify-content: space-between;
    list-style: none;
    text-decoration: none;
    place-items: center;
}

nav ul li a {
    display: inline-block;
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 0 10px;
    color: #fff;
    letter-spacing: 0.2px;
    cursor: pointer;
    line-height: 55px;
    transition: all 0.25s ease-in-out;
}

nav ul li a:hover {
    background-color: var(--app-button-hover-color);
}

input[type=button],
input[type=submit] {
    width: 115px;
    height: 35px;
    border-radius: 5px;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    transition: all 0.25s ease-in-out;

}

input[type=button]:hover {
    background-color: var(--app-button-hover-color);
}

.addBtn,
.submit,
.returnBtn {
    background-color: var(--app-button-color);
    border: 1px solid var(--app-button-color);
}

/*----------------------------------------*/
/*  Catalog                               */
/*----------------------------------------*/

.sort-div {
    text-align: right;
    margin-top: 38px;
}

.sort-div .sorting {
    background-color: #e23f3f;
}

.catalog {
    /* margin-top: 20px; */
    display: grid;
    grid-template-columns: 0.36fr 0.64fr;
    gap: 10px;
    place-items: center;

}

.result-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(250px, 1fr));
    gap: 30px;
    margin-top: 35px;
    margin-bottom: 35px;
}

.catalog p {
    color: var(--app-light-gray);
}

.character-name {
    font-size: 14px;
    margin-top: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.character-name p {
    font-size: 17px;

}

figure img {
    border-radius: 5px;
    height: 30px;
    width: 50px;
}

.character-image {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.character-item {
    padding: 3px;
    border: 1px solid;
    text-decoration: none;
    transition: all 0.70s ease-in-out;
}

.elipse {
    width: 50%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.gender {
    width: 50%;
    text-align: right;
}

.character-item:hover {
    cursor: pointer;
    transform: translateY(-5px);
    box-shadow: -1px 6px 25px 8px rgba(0, 0, 0, 0.56);
}

a {
    text-decoration: underline;
}


/*----------------------------------------*/
/*Footer                                  */
/*----------------------------------------*/

footer {
    /* margin-top: 30px; */
    height: 55px;
    width: 100%;
    background-color: var(--app-medium-gray);
    color: var(--app-light-gray);
    font-size: 12px;
    font-weight: 660;
    text-align: center;
    place-content: center;
    line-height: 55px;
    /* position: fixed; */
}

/*----------------------------------------*/
/*Page Not Found                          */
/*----------------------------------------*/

.notFound {
    height: calc(100vh - 110px);
    background: #fff url("https://media4.giphy.com/media/l1J9EdzfOSgfyueLm/200w.webp?cid=ecf05e472dslr5bz5e0cmnijkxnbgeuk2dp8qfbtgotjvid9&ep=v1_gifs_search&rid=200w.webp&ct=g") center center / cover no-repeat;
    display: grid;
    place-items: center;
}

.notFound img {
    border-radius: 20px;
    padding: 15px;
}

.content {
    text-align: center;
}

.content h1 {
    color: black;
    font-size: 35px;
}

.content p {
    color: black;
    font-size: 22px;
    font-weight: 900;
}

.notfound-message {
    font-size: 28px;
    font-weight: 800;
    text-transform: uppercase;
}




/* - - - - - - - - - - - - - - - - - - - - - - */
/* Detail                                      */
/* - - - - - - - - - - - - - - - - - - - - - - */
.center {
    display: grid;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 110px);
}

.center .name {
    color: white;
    place-content: center;
    text-align: center;
    margin-bottom: -150px;
    font-size: 45px;
    font-weight: bold;
    letter-spacing: 2px;
    color: var(--app-button-hover-color);
    font-family: 'Times New Roman', Times, serif;
}

.character-details {
    height: 50%;
    display: flex;
    gap: 20px;
    /* margin-top: -150px; */
}

.character-details figure img {
    margin-right: 80px;
    border: none;
    height: 100%;
    width: 100%;
    place-items: center;
    border-radius: 20px;

}

.character-details .detail {
    color: #fff;
    font-size: 1.8rem;
    margin-top: 25px;
}

.character-details .detail h2 {
    margin-bottom: 10px;
}

.character-details .detail p+p {
    margin-top: 10px;
}

.gray {
    color: #9e9e9e;
    font-weight: 300;
}

.character-details .detail p:last-child {
    margin-top: 10px;
}

/* - - - - - - - - - - - - - - - - - - - - - - */
/* new                                         */
/* - - - - - - - - - - - - - - - - - - - - - - */
.addForm {
    display: grid;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 110px);
    background: #fff url("https://wallpapers.com/images/hd/rick-and-morty-unamused-rick-agpuysmhk3lhdl4p.jpg") center center / cover no-repeat;
    place-items: center;

}

.addForm form {
    height: 80%;
}

.addForm h2 {
    margin-bottom: 30px;
    font-size: 4rem;
    color: white;
}

.addForm input,
.addForm select {
    height: 45px;
    width: 100%;
    border: none;
    background-color: rgba(60, 62, 69, 0.572);
    color: #fff;
    border-radius: 6px;
    padding: 10px;
    font-size: 1.6rem;
}

.addForm p {
    height: 25px;
    line-height: 25px;
    font-size: 1.4rem;
    color: #db5a6b;
}

.addForm select {
    margin-bottom: 25px;
}

.addForm input[type=submit] {
    background-color: #0085ff;
    color: #fff;
    height: 45px;
    width: 100%;
    margin-left: -1px;
}

.message p {
    color: red;
}

.addForm input[type=submit]:hover {
    background-color: var(--app-button-color);
}

.addForm input[type=submit]:disabled {
    background-color: var(--app-button-hover-color);
}